/**
 * @returns {String} https://cdn.jsdelivr.net/... /
 */
function getDir() {
  var thisScript = document.currentScript.src;
  var thisUrl = thisScript.split('/');
  //  /dist/*.js
  // 
  thisUrl.pop();
  thisUrl.pop();
  thisUrl = thisUrl.join('/');
  return thisUrl;
}

/**
 * @constant {String} _dir CDN URL
 */
const _dir = getDir();

module.exports = _dir;