const pluginsIndex = require('../plugins/index.json');
const _dir = require('../method/_dir');
const _msg = require('./_msg');

/**
 * @module pluginStore InPageEdit
 */
var pluginStore = {
  /**
   * @module pluginStore.get 
   */
  get() {
    ssi_modal.show({
      className: 'in-page-edit plugins-store',
      sizeClass: 'dialog',
      center: true,
      title: _msg('plugins-store-title'),
      content: 'Under development...',
      buttons: [{
        label: _msg('ok'),
        className: 'btn btn-single',
        method(a, modal) {
          modal.close();
        }
      }]
    })
  },
  /**
   * @module pluginStore.load 
   * @param {String} name 
   */
  load(name) {
    if (pluginsIndex[name]) {
      mw.loader.load(_dir + '/plugins/' + name);
    } else {
      console.warn('[InPageEdit] ', name);
    }
  }
}

module.exports = {
  pluginStore
}